import { NzModalService } from 'ng-zorro-antd/modal';
import { ICON_PATH } from './../../../const/icon-path.const';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { RecommendModalComponent } from '../modals/recommend-modal/recommend-modal.component';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Location } from '@angular/common';
import { TAB_PARAM } from '../../tab-param.const';

@Component({
  selector: 'app-project-info',
  templateUrl: './project-info.component.html',
  styleUrls: ['./project-info.component.scss']
})
export class ProjectInfoComponent implements OnInit {
  @Input() projectId?: number;
  @Output() showProjectList = new EventEmitter<boolean>();
  get ICON_PATH() {
    return ICON_PATH;
  }
  constructor(
    private nzModal: NzModalService,
  ) { }

  ngOnInit(): void {
    console.log(this.projectId);
  }

  onBackList(): void {
    // 顯示list，隱藏info
    this.showProjectList.emit(true);
  }
  /**
   * 推薦人員modal
   */
  onRecommend(): void {
    this.nzModal.create({
      nzContent: RecommendModalComponent,
      nzWidth: 600,
      nzMaskClosable: false,
      nzFooter: null,
      nzComponentParams: { projectId: this.projectId },
      nzBodyStyle: { padding: '12px 24px' }
    });
  }

}
