import { Router } from '@angular/router';
import { errorMsg } from 'projects/tc-lib/src/public-api';
import { MessageService } from 'merc-lib';
import { ICON_PATH } from './../../../../const/icon-path.const';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MODAL_ICON_PATH } from '../../../../const/img-path.const';
import { TAB_PARAM } from '../../../tab-param.const';

@Component({
  selector: 'app-recommend-modal',
  templateUrl: './recommend-modal.component.html',
  styleUrls: ['./recommend-modal.component.scss']
})
export class RecommendModalComponent implements OnInit {
  @Input() projectId?: number;
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  mockData = [{ value: 'A', id: 'F12134566' }, { value: 'B', id: 'A123456789' }];

  errorMsg = errorMsg;

  validateForm: FormGroup = this.fb.group({
    name: ['', Validators.required],
    class: [''],
    location: [{ value: '台中', disabled: true }],
    id: [{ value: '', disabled: true }]
  });
  constructor(
    public modalRef: NzModalRef,
    private fb: FormBuilder,
    private messageService: MessageService,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  onConfirmClick(): void {
    const formValue = this.validateForm.getRawValue();
    this.modalRef.triggerOk();
    const confirmModal = this.messageService.successModal(`您的推薦的<span class="text-secondary">學員${formValue.name}</span>
    <span class="text-secondary">身分證字號${formValue.id}</span>，
    已成功送出申請！
    `, { msgTitle: '推薦名單已送出', imgPath: MODAL_ICON_PATH.SUCCESS, positiveBtnTitle: '確認', isHtmlCode: true });
    confirmModal.afterClose.subscribe(() => {
      this.router.navigate([], { queryParams: { tab: TAB_PARAM.PARTICIPATION_LIST, projectId: this.projectId } });
    });

  }

  selectTrainee(value: string): void {
    const trainee = this.mockData.find(d => d.value === value);
    this.validateForm.get('id')?.patchValue(trainee?.id);
  }
}
