
import { SharedService } from './../../shared/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Tab } from 'merc-lib';
import { TAB_PARAM } from './tab-param.const';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
  get TAB_PARAM(): typeof TAB_PARAM {
    return TAB_PARAM;
  }
  /** tab序號 */
  selectedIndex = 0;
  tabList: Tab[] = [
    {
      id: 0,
      tabName: '專案列表'
    },
    {
      id: 1,
      tabName: '轄下已參加名單'
    }
  ];
  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private sharedService: SharedService
  ) { }

  ngOnInit(): void {
    this.setInitParam((params: any) => {
      if (!params.tab) {
        this.router.navigate([],
          { queryParams: { tab: TAB_PARAM.PROJECT_LIST } });
      }
    });
  }



  /**
   * 取得 competition_id
   * @param callback 取得queryParam後答覆
   */
  private setInitParam(callback: any): void {
    this.activatedRoute.queryParams.subscribe(params => {
      callback(params);
    });
  }
  /** 選取tab序號 */
  selectTab(index: number): void {
    this.selectedIndex = index;
  }
}
